import axios from "axios"

export const getLinks=(user_id)=>async(dispatch)=>{
    const config = {
        headers: {
          "Content-Type": "application/json",
        },
      };
      
    const id={uid:user_id}
    const body=JSON.stringify({uid:user_id, name:"Eyasu Emana"})
    try {
        const res = await axios.post(`${process.env.REACT_APP_API_URL}/api/service/get-links/`,body, config)
        dispatch({
            type:"LINKS_LOAD_SUCCESS",
            payload:JSON.stringify(res.data)
        })
    } catch (error) {
        dispatch({
            type:"LINKS_LOAD_FAIL"
        })
    }
}
export const addLinkAction=(body)=>async(dispatch)=>{
    const config = {
        headers: {
          "Content-Type": "application/json",
        },
      };
    const body=JSON.stringify(body)
    try {
        const res=axios.post(`${process.env.REACT_APP_API_URL}/api/service/add-link/`,body,config);
    dispatch({
        type:"ADD_LINK_SUCCESS",
        payload:""
    })
    } catch (error) {
        dispatch({
            type:"ADD_LINK_FAIL",
            payload:"",
    })
}
}