import { React, useState, useEffect } from "react";
import {
  Container,
  Grid,
  IconButton,
  Paper,
  Skeleton,
  Stack,
  Typography,
} from "@mui/material";
import Swal from "sweetalert2";
import { Link } from "react-router-dom";
import Divider from "@mui/material/Divider";
import ContentCopyIcon from "@mui/icons-material/ContentCopy";
import DeleteIcon from "@mui/icons-material/Delete";
import DoneAllIcon from "@mui/icons-material/DoneAll";
function LinkCard({ LinkList, linkFetch, isLoaded }) {
  const [isCoppied, setIsCoppied] = useState();
  function getCookie(name) {
    var cookieValue = null;
    if (document.cookie && document.cookie !== "") {
      var cookies = document.cookie.split(";");
      for (var i = 0; i < cookies.length; i++) {
        var cookie = cookies[i].trim();
        // Does this cookie string begin with the name we want?
        if (cookie.substring(0, name.length + 1) === name + "=") {
          cookieValue = decodeURIComponent(cookie.substring(name.length + 1));
          break;
        }
      }
    }
    return cookieValue;
  }

  function deleteLink(link) {
    // var csrftoken = this.getCookie('csrftoken')
    fetch(`http://127.0.0.1:8000/api/service/link-delete/${link.id}/`, {
      method: "DELETE",
      headers: {
        "Content-type": "application/json",
        // 'X-CSRFToken':csrftoken,
      },
    }).then((response) => {
      linkFetch();
    });
  }
  function copy(event) {
    setIsCoppied(true);
  }
  return (
    <div>
      <Grid container>
        {LinkList.map((link, index) => (
          <Grid item sm={12} md={6} lx={3} key={index}>
              <Paper
                elevation={4}
                sx={{
                  background:
                    "linear-gradient(45deg, #5E6B8B 30%, #FF8E53 90%)",
                  width: 380,
                  p: 1,
                  m: 2,
                  borderRadius: 3,
                  height: 100,
                  border: "1px solid",
                  borderColor: "primary.dark",
                }}
              >
                <Stack
                  sx={{ paddingLeft: "5px" }}
                  direction="row"
                  justifyContent="center"
                  alignItems="center"
                  spacing={2}
                  divider={
                    <Divider
                      sx={{ backgroundColor: "white" }}
                      orientation="vertical"
                      flexItem
                    />
                  }
                >
                  <div style={{ width: "85%" }}>
                    {link.title}
                    <br />
                    <Typography noWrap>
                      <Link
                        style={{ textDecoration: "none", color: "white" }}
                        to={`${link.link}`}
                      >
                        {link.link}
                      </Link>
                    </Typography>
                  </div>

                  <div style={{ width: "15%" }}>
                    <IconButton
                      onClick={() => {
                        copy(index);
                      }}
                    >
                      {!isCoppied ? (
                        <ContentCopyIcon sx={{ color: "white" }} />
                      ) : (
                        <DoneAllIcon />
                      )}
                    </IconButton>
                    <IconButton
                      onClick={() =>
                        Swal.fire({
                          title: "Are you sure?",
                          text: "You won't be able to revert this!",
                          icon: "warning",
                          showCancelButton: true,
                          confirmButtonColor: "#3085d6",
                          cancelButtonColor: "#d33",
                          confirmButtonText: "Yes, delete it!",
                        }).then((result) => {
                          if (result.isConfirmed) {
                            deleteLink(link);
                            Swal.fire({
                              position: "center",
                              icon: "success",
                              title: "Link has been successfully deleted",
                              showConfirmButton: false,
                              timer: 1500,
                            });
                          }
                        })
                      }
                    >
                      {/* deleteLink(link) */}
                      <DeleteIcon sx={{ color: "#F44" }} />
                    </IconButton>
                  </div>
                </Stack>
              </Paper>
            
          </Grid>
        ))}
      </Grid>
    </div>
  );
}

export default LinkCard;
