import React, { useEffect, useState } from "react";
import TextField from "@mui/material/TextField";
import ContentPasteIcon from '@mui/icons-material/ContentPaste';
import {
  Autocomplete,
  Button,
  FormHelperText,
  Grid,
  IconButton,
  InputAdornment,
  Snackbar,
  Stack,
} from "@mui/material";
import SaveIcon from "@mui/icons-material/Save";
import BackspaceIcon from "@mui/icons-material/Backspace";
import { useNavigate } from "react-router-dom";
import CloseIcon from "@mui/icons-material/Close";
import LinkCard from "../linkCards/LinkCard";
import { connect, useSelector } from "react-redux";
import { addLinkAction } from "../../actions/linkActions";

function AddLink({addLinkAction, categories,linkFetch }) {
  
  const [title, setTitle] = useState("");
  const [description, setDescription] = useState("");
  const [link, setLink] = useState("");
  const [category, setCategory] = useState("");
  const [show, setshow] = useState(false);
  const [openSnack, setOpenSnack] = useState(false);
  const [snackmessage, setSnackMessage] = useState("");
  const [user_id,setUserId]=useState(0);
  const categoryOption = categories.map((cat, index) => ({
    id: cat.id,
    label: cat.cat,
  }));
  const addlinkstate=useSelector((state=>state.authReducer));
  const {user}=addlinkstate
// var user_id;
  // const userstate=useSelector((state)=>state.authReducer)
  // const currentUser=userstate.state.id;

//   useEffect(() => {
//     const currentUser=JSON.parse(localStorage.getItem("currentUser")).id
//     // user_id=currentUser
//     setUserId(currentUser)
// }, []);

  const mydata = {
    id: null,
    title: title,
    link: link,
    description: description,
    owner:user.id,
    category: category.id,
  };

  // owner:JSON.parse(localStorage.getItem("currentUser")).id,

  // console.log(mydata);
  // category.id,
  const navigate = useNavigate();
  const handleClose = () => {
    setOpenSnack(!openSnack);
  };

  const getCookie = (name) => {
    var cookieValue = null;
    if (document.cookie && document.cookie !== "") {
      var cookies = document.cookie.split(";");
      for (var i = 0; i < cookies.length; i++) {
        var cookie = cookies[i].trim();
        // Does this cookie string begin with the name we want?
        if (cookie.substring(0, name.length + 1) === name + "=") {
          cookieValue = decodeURIComponent(cookie.substring(name.length + 1));
          break;
        }
      }
    }
    return cookieValue;
  };
  
  const handleSubmit = (e) => {
    e.preventDefault();
    addLinkAction(mydata)
    // var csrftoken = getCookie("csrftoken");
    // var url = "http://127.0.0.1:8000/api/service/add-link/";
    // fetch(url, {
    //   method: "POST",
    //   headers: {
    //     "Content-type": "application/json",
    //     "X-CSRFToken": csrftoken,
    //   },
    //   body: JSON.stringify(mydata),
    // })
    //   .then((response) => {
    //     linkFetch()
    //     setSnackMessage("Saved successfully!");
    //     setOpenSnack(true);
    //   })
    //   .catch((error) => {
    //     setSnackMessage("Something went wrong, try again/n" + error);
    //   });
  };
  return (
    <form onSubmit={handleSubmit}>
      <Stack spacing={2}>
        <TextField
          id="outlined-basic"
          label="Add Title"
          variant="outlined"
          color="warning"
          onChange={(e) => setTitle(e.target.value)}
        />
        <TextField
          id="outlined-basic"
          label="Add Link"
          variant="outlined"
          color="warning"
          required
          onChange={(e) => setLink(e.target.value)}
          InputProps={{
            endAdornment: <InputAdornment position="end"><IconButton><ContentPasteIcon/></IconButton></InputAdornment>
          }}
        />
        <FormHelperText error>The above field is required *</FormHelperText>
        <TextField
          multiline
          id="outlined-basic"
          label="Description"
          
          rows={3}
          variant="outlined"
          color="warning"
          onChange={(e) => setDescription(e.target.value)}
        />

        <Autocomplete
          value={category}
          sx={{color:"warning"}} 
          onChange={(event, value) => {
            setCategory(value);
          }}
          options={categoryOption}
          renderInput={(params) => <TextField {...params} label="Category" />}
        />
        <Snackbar
          message={snackmessage}
          autoHideDuration={3000}
          open={openSnack}
          onClose={handleClose}
          action={<CloseIcon onClick={handleClose} />}
        />

        <Grid
          container
          direction="row"
          justifyContent="flex-end"
          alignItems="flex-end"
          spacing={2}
        >
          <Grid item>
            <Stack direction="row" spacing={2}>
              <Button
                startIcon={<BackspaceIcon />}
                variant="outlined"
                color="error"
                size="medium"
                onClick={() => navigate("/")}
              >
                {" "}
                Back
              </Button>
              <Button
                startIcon={<SaveIcon />}
                variant="outlined"
                type="submit"
                color="info"
                size="medium"
              >
                Save
              </Button>
            </Stack>
            <Stack>{show && <h1>{mydata.title}</h1>}</Stack>
          </Grid>
        </Grid>
      </Stack>
    </form>
  );
}

export default connect(null,{addLinkAction}) (AddLink);
