const initialState={
    links:[],
    islinkloaded:false,
    isLinkAdded:false
}
export const linkReducer=(state=initialState,action)=>{
    const {type,payload}=action;
    switch(type){
        case "LINKS_LOAD_SUCCESS":
           return {
             ...state,
            links:JSON.parse(payload),
            islinkloaded:true
           }
        case "LINKS_LOAD_FAIL":
            return {
                ...state,
                islinkloaded:false
            }
        case "LINK_ADD_SUCCESS":
            return {
                ...state,
                isLinkAdded:true
            }
            case "LINK_ADD_FAIL":
                return {
                    ...state,
                    isLinkAdded:false
                }
        default:
            return state
         
    }
}