import { React, useState, useEffect } from "react";
import PropTypes from "prop-types";
import Box from "@mui/material/Box";
import IconButton from "@mui/material/IconButton";
import InputBase from "@mui/material/InputBase";
import Divider from "@mui/material/Divider";
import MenuIcon from "@mui/icons-material/Menu";
import SortIcon from "@mui/icons-material/Sort";
import {
  CircularProgress,
  Container,
  FormControl,
  FormControlLabel,
  FormLabel,
  Grid,
  InputAdornment,
  Paper,
  Radio,
  RadioGroup,
  Stack,
  TextField,
} from "@mui/material";
import AddCircleIcon from "@mui/icons-material/AddCircle";
import CategoryIcon from "@mui/icons-material/Category";
import { Link } from "react-router-dom";
import { ThemeProvider } from "@mui/system";
import LinkCard from "../linkCards/LinkCard";
import AddLink from "../addlink/AddLink";
import { Outlet } from "react-router-dom";
import { useNavigate } from "react-router-dom";
import SearchIcon from "@mui/icons-material/Search";

export default function Content(props) {
  const navigate = useNavigate();
  const [lookUp, setLookUp] = useState(null);
  const [fullData, setFUllData] = useState([]);
  useEffect(() => {
    setFUllData(props.LinkList);
  }, []);

  return (
    <div className="container mt-3">
      <div className="row justify-content-center">
        <div className="col-10">
          {/* <div className="d-flex justify-content-around bg-secondary p-lg-3 p-xs-5"> */}
          <Stack
            direction="row-reverse"
            justifyContent="space-evenly"
            alignItems="center"
            spacing={2}
            sx={{ p: 2, backgroundColor: "#f3e5f5" }}
          >
            <Paper
              component="form"
              sx={{
                p: "2px 4px",
                display: "flex",
                alignItems: "center",
                width: 400,
              }}
            >
              <IconButton sx={{ p: "10px" }} aria-label="menu">
                <MenuIcon />
              </IconButton>
              <InputBase
                sx={{ ml: 1, flex: 1 }}
                placeholder="Search links"
                inputProps={{ "aria-label": "search links" }}
                onChange={(e) => {
                  setLookUp(e.target.value);
                }}
              />
              <IconButton
                type="button"
                sx={{ p: "10px" }}
                aria-label="search"
                onClick={() => {
                  if (true) {
                    props.setLinkList(
                      props.LinkList.filter((search) =>
                        search.title.toLowerCase().includes(lookUp)
                      )
                    );
                  } else {
                    props.setLinkList(fullData);
                  }
                }}
              >
                <SearchIcon />
              </IconButton>
              <Divider
                sx={{ height: 28, m: 0.5 }}
                orientation="vertical"
                color="#345"
              />
              <IconButton
                color="primary"
                sx={{ p: "10px" }}
                aria-label="directions"
              >
                <SortIcon />
              </IconButton>
            </Paper>
            <div>
              <IconButton
                aria-label="add-link"
                onClick={(event) => {
                  navigate("/add-link");
                }}
              >
                <AddCircleIcon fontSize="large" color="warning" />
              </IconButton>
              <IconButton aria-label="add-category" color="success">
                <CategoryIcon fontSize="large" />
              </IconButton>
            </div>
          </Stack>
          {/* </div> */}
        </div>
      </div>
      <div className="row justify-content-center mt-3">
        <div className="col-10">
          {props.isLoaded ? (
            <div style={{ width: "100%" }}></div>
          ) : (
            <CircularProgress color="success" />
          )}
          <Outlet />
        </div>
      </div>
    </div>
  );
}
